body {
  text-align: center;
  max-height: 100vh;
  background: #f1e767;
  background: -moz-radial-gradient(center, ellipse cover, #f1e767 0%, #feb645 74%, #feb645 100%);
  background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, #f1e767), color-stop(74%, #feb645), color-stop(100%, #feb645));
  background: -webkit-radial-gradient(center, ellipse cover, #f1e767 0%, #feb645 74%, #feb645 100%);
  background: -o-radial-gradient(center, ellipse cover, #f1e767 0%, #feb645 74%, #feb645 100%);
  background: -ms-radial-gradient(center, ellipse cover, #f1e767 0%, #feb645 74%, #feb645 100%);
  background: radial-gradient(ellipse at center, #f1e767 0%, #feb645 74%, #feb645 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f1e767', endColorstr='#feb645', GradientType=1 ); }

.border-bottom {
  border-bottom: 1px solid #7f7f7f; }

img {
  width: 100%; }

header {
  max-width: 90%;
  margin: 0 auto;
  text-align: center; }
  header h1 {
    margin-top: 60px;
    margin-bottom: 100px; }

footer {
  text-align: center; }
  footer a {
    font-size: 1.6em;
    text-decoration: none;
    font-weight: 400;
    color: rgba(0, 151, 207, 0.72); }
    footer a:hover {
      color: #fff; }

* {
  margin: 0; }

html, body {
  height: 100%; }

.page-wrap {
  min-height: 100%;
  /* equal to footer height */
  margin-bottom: -142px; }

.page-wrap:after {
  content: "";
  display: block; }

.site-footer, .page-wrap:after {
  height: 142px; }

body {
  font-family: 'Lato', sans-serif; }

.list {
  font-size: 1.2em;
  font-weight: normal;
  line-height: 1.6;
  color: white; }

h1 {
  font-size: 4em;
  line-height: .9; }
  @media screen and (min-width: 1024px) {
    h1 {
      letter-spacing: -1px;
      font-size: 5em;
      line-height: 1.2; } }

h2 {
  font-size: 2em; }

.capitalize {
  text-transform: capitalize; }

.results {
  padding-top: 50px; }

#the-basics {
  opacity: 0;
  animation-name: appear;
  animation-duration: 300ms;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards; }

.twitter-typeahead {
  margin: 0 auto; }

.typeahead,
.tt-query,
.tt-hint {
  width: 60%;
  height: 60px;
  padding: 8px 12px;
  font-size: 2em;
  line-height: 30px;
  border: 2px solid #ccc;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  outline: none;
  color: #fff; }

.tt-query {
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075); }

.tt-hint {
  color: #fff;
  visibility: hidden; }

.typeahead.tt-input {
  border: 0;
  color: #fff;
  font-size: 1.8em; }
  @media screen and (min-width: 768px) {
    .typeahead.tt-input {
      font-size: 2.2em; } }

.typeahead.tt-input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #fff; }

.tt-menu {
  width: 80%;
  margin: 12px 0;
  padding: 8px 0;
  left: 40px !important;
  background-color: rgba(0, 151, 207, 0.72);
  color: #fff;
  border-radius: 8px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2); }
  @media screen and (min-width: 1024px) {
    .tt-menu {
      left: 0 !important;
      width: 100%; } }

.tt-suggestion {
  padding: 3px 20px;
  font-size: 20px;
  line-height: 1.6; }

.tt-suggestion:hover {
  cursor: pointer;
  color: #fff;
  background-color: #0097cf; }

.tt-suggestion.tt-cursor {
  color: #fff;
  background-color: #0097cf; }

.tt-suggestion p {
  margin: 0; }

.list {
  width: 90%;
  list-style: none;
  perspective: 1000px; }
  .list li {
    font-size: 1.2em;
    position: relative;
    margin-bottom: 30px;
    padding-top: 50px;
    padding-bottom: 50px;
    border-radius: 8px;
    background-color: rgba(0, 151, 207, 0.72);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2); }
    .list li:nth-child(1) {
      animation-name: appear;
      animation-duration: 300ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li:nth-child(2) {
      animation-name: appear;
      animation-duration: 600ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li:nth-child(3) {
      animation-name: appear;
      animation-duration: 900ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li:nth-child(4) {
      animation-name: appear;
      animation-duration: 1200ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li:nth-child(5) {
      animation-name: appear;
      animation-duration: 1500ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li:nth-child(6) {
      animation-name: appear;
      animation-duration: 1800ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li:nth-child(7) {
      animation-name: appear;
      animation-duration: 2100ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li:nth-child(8) {
      animation-name: appear;
      animation-duration: 2400ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li:nth-child(9) {
      animation-name: appear;
      animation-duration: 2700ms;
      animation-timing-function: ease-in-out;
      animation-fill-mode: forwards; }
    .list li img.weather-icon {
      position: absolute;
      top: 0;
      left: 50%;
      margin-left: -30px;
      margin-top: 5px; }
      @media screen and (min-width: 768px) {
        .list li img.weather-icon {
          top: 50%;
          left: 75%;
          margin-left: 0;
          margin-top: -30px; } }
  @media screen and (min-width: 768px) {
    .list {
      width: 60%;
      margin: 0 auto; } }
  @media screen and (min-width: 1024px) {
    .list {
      width: 40%; } }
  @media screen and (min-width: 1280px) {
    .list {
      width: 30%; } }

.weather-icon {
  margin-left: 20px;
  margin-top: 30px;
  width: 60px;
  display: inline-block; }

@keyframes appear {
  0% {
    opacity: 0;
    transform: rotateY(90deg) translate(30px); }
  100% {
    opacity: 1;
    transform: rotateY(0deg) translate(0px); } }
